import React, { useState, useEffect } from "react";
import styled from "styled-components";
import { Button } from "react-bootstrap";

import { firebaseapp } from "../../utils";
import "firebase/storage";

const InteriorDecorationsContainer = styled.div`
	display: flex;
	flex-direction: ${props => (props.theme.screens.mmd ? "column-reverse" : "row")};
	flex-wrap: wrap;
	padding: ${props => (props.theme.screens.mmd ? "3vh" : "7vh")};
	justify-content: space-between;
	align-items: center;
	background-image: ${props => (props.theme.screens.mmd ? "none" : "url('/images/white_bg_2.svg')")};
	background-repeat: no-repeat;
`;

const Gallery = styled.div`
	display: ${props => (props.theme.screens.mmd ? "flexbox" : "grid")};
	flex-direction: column;
	flex-wrap: wrap;
	grid-template-columns: repeat(8, 1fr);
	grid-template-rows: repeat(8, 5vw);
	grid-gap: 15px;
	width: ${props => (props.theme.screens.mmd ? "90vw" : "50vw")};
	padding: ${props => (props.theme.screens.mmd ? "1rem" : "0rem")};
`;

const TextContainer = styled.div`
	display: flex;
	flex-direction: column;
	flex-wrap: wrap;
	align-content: ${props => (props.theme.screens.sm ? "center" : "left")};
	padding: ${props => (props.theme.screens.mmd ? "1.3rem" : "2rem")};
	flex: 1;
`;

const TextBig = styled.h1`
	font-family: "Open Sans", sans-serif;
	font-weight: 700;
	font-size: ${props => (props.theme.screens.mmd ? "2rem" : "2.3rem")};
	text-align: ${props => (props.theme.screens.mmd ? "center" : "left")};
	display: inline-block;
	border: ${props => (props.theme.screens.mmd ? "3px solid black" : "none")};
	border-bottom: 3px solid black;
	padding: ${props => (props.theme.screens.mmd ? "1.2rem" : "0rem 0rem 1.2rem 0rem")};
`;

const TextSmall = styled.h3`
	font-family: "Crimson Text", serif;
	font-size: 1.4rem;
	font-weight: 400;
	margin: 2rem 0rem 0rem 0rem;
	font-style: ${props => (props.italic ? "italic" : "normal")};
`;

const Img = styled.img`
	height: 100%;
	width: 100%;
	object-fit: cover;
	grid-column-start: ${props => props.colStart};
	grid-column-end: ${props => props.colEnd};
	grid-row-start: ${props => props.rowStart};
	grid-row-end: ${props => props.rowEnd};
	margin: ${props => (props.theme.screens.mmd ? "1rem 0rem 1rem 0rem" : "0rem")};
`;

const InteriorDecorations: React.FC = () => {
	const [imgs, setImgs] = useState([]);

	useEffect(() => {
		firebaseapp
			.storage()
			.ref("landing_page/interior_decorations")
			.listAll()
			.then(({ items }) => {
				Promise.all(items.map(imgRef => imgRef.getDownloadURL())).then(urls => {
					setImgs(urls);
				});
			});
	}, []);

	return (
		<InteriorDecorationsContainer>
			<Gallery>
				<Img src={imgs[0]} colStart="2" colEnd="4" rowStart="1" rowEnd="5"></Img>
				<Img src={imgs[1]} colStart="1" colEnd="4" rowStart="5" rowEnd="7"></Img>
				<Img src={imgs[2]} colStart="4" colEnd="8" rowStart="1" rowEnd="3"></Img>
				<Img src={imgs[3]} colStart="2" colEnd="4" rowStart="7" rowEnd="9"></Img>
				<Img src={imgs[4]} colStart="4" colEnd="7" rowStart="3" rowEnd="7"></Img>
				<Img src={imgs[5]} colStart="4" colEnd="9" rowStart="7" rowEnd="9"></Img>
			</Gallery>
			<TextContainer>
				<TextBig>Interior Decorations</TextBig>
				<TextSmall>
					Interior design is the art and science of enhancing the interior of a building to achieve a
					healthier and more aesthetically pleasing environment for the people using the space. We provide you
					the best in class Interior Decorations for Homes, Offices, Work Places, Cafes, Hotels, Resturants
					and etc. We provide you the best in class Interior Decorations for your living as interior
					decoration has now become a major part of lifestyle. <br></br>
					<br></br>
					Who doesn't wants to live in a better way? Everyone wants their home surroundings and workplaces
					decorated and good looking we here are to help you and make your dream go true of a Decorated a
					home, surrounding and workplace. <br></br>
					<br></br>
					
					Make your dream Decorations come true. Contact us for more info.
				</TextSmall>
				<br></br>
				<Button variant="warning" style={{ width: "10rem" }} href="/interior-decorations">
					Learn More
				</Button>
			</TextContainer>
		</InteriorDecorationsContainer>
	);
};

export default InteriorDecorations;
