import React, { useState, useEffect } from "react";
import styled from "styled-components";
import { Button } from "react-bootstrap";

import { firebaseapp } from "../../utils/";
import "firebase/storage";

const FPContainer = styled.div`
	display: flex;
	flex-direction: ${props => (props.theme.screens.mmd ? "column" : "row")};
	flex-wrap: wrap;
	padding: ${props => (props.theme.screens.mmd ? "3vh" : "7vh")};
	justify-content: space-between;
	align-items: center;
	background-image: url("/images/white_bg.svg");
	background-repeat: no-repeat;
`;

const Gallery = styled.div`
	display: ${props => (props.theme.screens.mmd ? "flexbox" : "grid")};
	flex-direction: column;
	flex-wrap: wrap;
	grid-template-columns: repeat(8, 1fr);
	grid-template-rows: repeat(10, 5vw);
	grid-gap: 15px;
	width: ${props => (props.theme.screens.mmd ? "90vw" : "50vw")};
	padding: ${props => (props.theme.screens.mmd ? "1rem" : "2rem")};
`;

const TextContainer = styled.div`
	display: flex;
	flex-direction: column;
	flex-wrap: wrap;
	align-content: ${props => (props.theme.screens.sm ? "center" : "left")};
	padding: ${props => (props.theme.screens.mmd ? "1.3rem" : "0rem")};
	width: ${props => (props.theme.screens.mmd ? "100%" : "30vw")};
`;

const TextBig = styled.h1`
	font-family: "Open Sans", sans-serif;
	font-weight: 700;
	font-size: ${props => (props.theme.screens.mmd ? "2rem" : "2.3rem")};
	text-align: ${props => (props.theme.screens.mmd ? "center" : "left")};
	display: inline-block;
	border: ${props => (props.theme.screens.mmd ? "3px solid black" : "none")};
	border-bottom: 3px solid black;
	padding: ${props => (props.theme.screens.mmd ? "1.2rem" : "0rem 0rem 1.2rem 0rem")};
`;

const TextSmall = styled.h3`
	font-family: "Crimson Text", serif;
	font-size: 1.4rem;
	font-weight: 400;
	margin: 2rem 0rem 0rem 0rem;
	font-style: ${props => (props.italic ? "italic" : "normal")};
`;

const Img = styled.img`
	height: 100%;
	width: 100%;
	object-fit: cover;
	grid-column-start: ${props => props.colStart};
	grid-column-end: ${props => props.colEnd};
	grid-row-start: ${props => props.rowStart};
	grid-row-end: ${props => props.rowEnd};
	margin: ${props => (props.theme.screens.mmd ? "1rem 0rem 1rem 0rem" : "0rem")};
`;

const FashionAndPhotography: React.FC = () => {
	const [imgs, setImgs] = useState([]);

	useEffect(() => {
		firebaseapp
			.storage()
			.ref("landing_page/fashion_photography")
			.listAll()
			.then(({ items }) => {
				Promise.all(items.map(imgRef => imgRef.getDownloadURL())).then(urls => {
					setImgs(urls);
				});
			});
	}, []);

	return (
		<FPContainer>
			<TextContainer>
				<TextBig>Fashion and Photography</TextBig>
				<TextSmall>
					Fashion is a popular aesthetic expression at a particular period and place and in a specific
					context, especially in clothing, footwear, lifestyle, accessories, makeup, hairstyle, and body
					proportions. Fashion and Photography is a major part of lifestyle we here are for your grooming and
					for you get a great sense of fashion and lifestyle. <br></br>
					<br></br>
					
					Talking about fashion everyone wants to be fashionable these days and want to be complimented by
					everyone. With fashion you also need something to present your fashion to everyone and which can
					only be done by photography. If you grow your skills towards fashion a good photo will help you to
					present your fashion to the rest of world. We have a Professional and trained team of Models,
					Designer and Photographers in order to help you with fashion and lifestyle.
				</TextSmall>
				<br></br>
				<Button variant="warning" style={{ width: "10rem" }} href="/fashion-photography">
					Learn More
				</Button>
			</TextContainer>

			<Gallery>
				<Img src={imgs[0]} colStart="2" colEnd="4" rowStart="1" rowEnd="5"></Img>
				<Img src={imgs[4]} colStart="1" colEnd="4" rowStart="5" rowEnd="9"></Img>
				<Img src={imgs[1]} colStart="4" colEnd="8" rowStart="1" rowEnd="3"></Img>
				<Img src={imgs[3]} colStart="4" colEnd="7" rowStart="3" rowEnd="7"></Img>
				<Img src={imgs[2]} colStart="4" colEnd="9" rowStart="7" rowEnd="11"></Img>
			</Gallery>
		</FPContainer>
	);
};

export default FashionAndPhotography;
